import React, {useState} from "react";
import estilos from "./css/card.module.css";
import climaStyle from './css/climaState.module.css'
import {Link} from 'react-router-dom'

export default function Card({ id, name, min, max, onClose, img, weather }) {
  // ac치 va tu c칩digo


  function climaState() {
    if (weather === "Clear" && img.charAt(img.length - 1) === "d") {
      return climaStyle.climaCaluroso;
    } else if (weather === "Clear" && img.charAt(img.length - 1) === "n") {
      return climaStyle.calurosoNoche;
    }

    if (weather === "Clouds") return climaStyle.climaFrio;

    if (weather === "Thunderstorm" || weather === "Drizzle" || weather === 'Rain')
      return climaStyle.climaLluvioso;

    if (weather === "Smoke" ) return climaStyle.smoke;
    if (weather === "Mist") return climaStyle.mist;

    if(!weather) return climaStyle.sinClima
  }
  
  let imgURL = `http://openweathermap.org/img/wn/${img}@2x.png`;
  
  return (
    <div className={estilos.contenedor}>
      <div className={climaState()}>
        <div className={estilos.boton}>
          <button className={estilos.boton} onClick={onClose}>
            X
          </button>
        </div>
        <Link to={`/ciudad/${id}`} className={estilos.linkText}>
          <h4 className='text-dark' >{name}</h4>
        </Link>
        <div className={estilos.info}>
          <div>
            <h6 className="text-danger">Min</h6>
            <p className="text-right">
              {Math.round(min - 273.15)} <strong>C춿</strong>{" "}
            </p>
            <p className="text-right">
              {Math.round(min)} <strong>K춿</strong>
            </p>
            <p className="text-right">
              {(Math.round(min - 273.15) * 9) / 5 + 32} <strong>F춿</strong>
            </p>
          </div>
          <div>
            <h6 className="text-warning">Max</h6>
            <p className={estilos.text}>
              {Math.round(max - 273.15)} <strong>C춿</strong>
            </p>
            <p className={estilos.text}>
              {Math.round(max)} <strong>K춿</strong>
            </p>
            <p className={estilos.text}>
              {(Math.round(max - 273.15) * 9) / 5 + 32} <strong>F춿</strong>
            </p>
          </div>
          <img className={estilos.cardImg} src={imgURL} alt={name} />
        </div>
      </div>
    </div>
  );
}
