import React, { useState } from 'react';
import {Route} from 'react-router-dom'
import './App.css';
import Nav from '../components/Nav.jsx';
import Cards from '../components/Cards.jsx';
import Swal from 'sweetalert2'
import apiKey from '../apiKey'
import About from '../components/About'
import City from '../components/City'


function App() {
  const [cities, setCities] = useState([]);
  function onClose(id) {
    setCities(oldCities => oldCities.filter(c => c.id !== id));
  }
  function onSearch(ciudad) {
    //Llamado a la API del clima
    fetch(`http://api.openweathermap.org/data/2.5/weather?q=${ciudad}&appid=${apiKey}`)
      .then(r => r.json())
      .then((recurso) => {
        if(recurso.main !== undefined){
          const ciudad = {
            min: recurso.main.temp_min,
            max: recurso.main.temp_max,
            img: recurso.weather[0].icon,
            id: recurso.id,
            wind: recurso.wind.speed,
            temp: recurso.main.temp,
            name: recurso.name,
            weather: recurso.weather[0].main,
            clouds: recurso.clouds.all,
            latitud: recurso.coord.lat,
            longitud: recurso.coord.lon,
          };
          console.log(ciudad)
          if(cities.find((ciudad) => ciudad.id === recurso.id)) Swal.fire({
            title: `${recurso.name} ya esta mostrada en pantalla`,
            icon: "info",
            confirmButtonText: "Continuar",
          });
          else setCities(oldCities => [...oldCities, ciudad]);
        } else {
          Swal.fire({
            title: "Error, no se encuentra esta ciudad",
            text: "Verifica que lo hayas escrito correctamente...",
            icon: "question",
            confirmButtonText: "Intentalo de nuevo",
          });
        }
      });
  }
  
  function onFilter(ciudadId) {
    let ciudad = cities.filter(c => c.id === parseInt(ciudadId));
    if(ciudad.length > 0) {
        return ciudad[0];
    } else {
        return null;
    }
  }
  return (
    <div className='App'>
      <Route path='/' render={()=> <Nav onSearch={onSearch} />} />
      <Route path='/about' component={About} />
      <Route exact path='/' render={()=> <Cards cities={cities} onClose={onClose} />} />
      <Route exact path='/ciudad/:id' render={({match})=> (<City city={onFilter(match.params.id)} />)} />
    </div>
  );
}

export default App;
