import React from 'react'

export default function City ({city}) {
  return(
    <div className='mt-5'>
      <div className="h3">{city.name}</div>
      <div className="row">
        <div className="col col-sm-12 col md-8">
          <div className="h5">Temperatura: <span className="text-primary">{city.temp}</span> </div>
          <div className="h5">Clima: <span className="text-primary">{city.weather}</span> </div>
          <div className="h5">Viento: <span className="text-primary">{city.wind}</span> </div>
          <div className="h5">Cantidad de nubes: <span className="text-primary">{city.clouds}</span> </div>
          <div className="h5">Latitud: <span className="text-primary">{city.latitud}춿</span> </div>
          <div className="h5">Longitud: <span className="text-primary">{city.longitud}춿</span> </div>
        </div>
      </div>
    </div>
  )
}