import React from 'react';
import Card from './Card'
import estilos from './css/cards.module.css'


export default function Cards({cities, onClose}) {
  // ac치 va tu c칩digo
  // tip, pod칠s usar un map
  return (
    <div className={estilos.contenedor}>
      {cities.map((c)=>(
        <Card
        key={c.id}
        id={c.id}
        max={c.max}
        min={c.min}
        name={c.name}
        img={c.img}
        weather={c.weather}
        onClose={()=> onClose(c.id)}
        />
      ))}
    </div>
  )
};